import React, { Component } from "react";
import Tilt from "react-tilt";
import "./Contact.css";
export default class Contact extends Component {
  render() {
    return (
      <div class="contact1 anim">
        <div class="container-contact1">
          <div class="contact1-pic js-tilt" data-tilt>
            <Tilt className="Tilt" options={{ max: 45 }}>
              <img src={require("./1.png")} alt="IMG" />
            </Tilt>
          </div>
          <form class="contact1-form validate-form">
            <span class="contact1-form-title">Get in touch</span>

            <div
              class="wrap-input1 validate-input"
              data-validate="Name is required"
            >
              <input
                class="input1"
                type="text"
                name="name"
                placeholder="Name"
              />
              <span class="shadow-input1" />
            </div>

            <div
              class="wrap-input1 validate-input"
              data-validate="Valid email is required: ex@abc.xyz"
            >
              <input
                class="input1"
                type="text"
                name="email"
                placeholder="Email"
              />
              <span class="shadow-input1" />
            </div>

            <div
              class="wrap-input1 validate-input"
              data-validate="Subject is required"
            >
              <input
                class="input1"
                type="text"
                name="subject"
                placeholder="Subject"
              />
              <span class="shadow-input1" />
            </div>

            <div
              class="wrap-input1 validate-input"
              data-validate="Message is required"
            >
              <textarea class="input1" name="message" placeholder="Message" />
              <span class="shadow-input1" />
            </div>

            <div class="container-contact1-form-btn">
              <button class="contact1-form-btn">
                <span>
                  Send Email
                  <i class="fa fa-long-arrow-right" aria-hidden="true" />
                </span>
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  }
}



